        </el-badge>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%; height: calc(100vh-260px)"
      border
      :header-cell-style="{background:'#5f7686',color:'#fff'}"
    >
      <el-table-column prop="model" label="型号" align="center"> </el-table-column>
      <el-table-column prop="factory" label="厂家" align="center"> </el-table-column>
      <el-table-column prop="package_specification" label="封装规格" align="center">
      </el-table-column>
