/* eslint-disable */
<script>
import Constant from "../components/common/Constant.vue";

export default {
  data() {
    return {
      contact_name: "",
      contact_number: "",
      contact_note: "",
      input: "",
      tableData: [],
      dialogVisible: false,
      testDic: {
        model: "DP83848IVV/NOPB",
        factory: "TI",
        package_specification: "LQFP48",
        year_batch: "2021+",
        in_stock: 1800,
        price: [
          { x: "1 - 20", y: "$4" },
          { x: "21 - 200", y: "$40" },
          { x: "200 以上", y: "$4" },
        ],
        delivery_time: "3-5天",
        picture:
          "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        pictures: [
          "https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg",
          "https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg",
        ],
      },
      imgVisible: false,
      imgUrl: '',
      cartNumList: [],
      drawer: false,
      allNum:0
    };
  },
  mounted() {
    this.input = this.$route.query.id;
    this.searchBtnClicked();
  },
  methods: {
    searchBtnClicked() {
      // this.tableData = [];
      // this.tableData.push(this.testDic);
      this.searchProductByModel(this.input);
    },

    addProduct(product) {
      console.log("product:", product);
    },
    //添加到购物车
    addToCart(index) {
      this.cartNumList.push(this.tableData[index])
      this.tableData[index].visible = false
      this.allNum = this.allNum + this.tableData[index].num
      // console.log(this.tableData[index])
      this.pClose(index)
    },
    //咨询
    askForInfomation(param) {
      console.log(param);
      this.dialogVisible = true;
    },
    //展示图片
    openImgDialog(url) {
      this.imgVisible = true
      this.imgUrl = url
    },
    //根据model查询产品
    searchProductByModel(model) {
      console.log("searchProductByModel");

      let searchModelApi =
        Constant.SERVER_PRE_URL + "/product/search/?model=" + model;
      console.log("searchModelApi:", searchModelApi);

      this.$http
        .get(searchModelApi)
        .then((response) => {
          console.log(response.data);
          let json = response.data;
          if (json.errorCode == 0) {
            this.$message.success("搜索成功");
            this.tableData = json.content;
          } else {
            this.$message.error(json.text);
          }
          this.tableData.forEach(element => {
            element.num = 1
            element.visible = false
          });
        })
        .catch((error) => {
          this.$message.error("失败,code = 2");
          console.log(error);
        });
    },
    //关闭咨询框
    handleClose() {
      this.dialogVisible = false;
    },
    openCart() {
      this.drawer = true
      console.log('222')
    },
    handleChange(index,val) {
      this.cartNumList[index].num = val
    },
    deletClick(index){
      this.cartNumList.splice(index, 1)
    },
    pClose(index) {
      this.$refs[`popover-` + index].doClose()
    }
  },
};
</script>

